import ewf from 'ewf';
import EwfProgressBarController from './ewf-progress-bar-controller';

ewf.directive('ewfProgressBar', EwfProgressBar);

export default function EwfProgressBar() {
    return {
        restrict: 'E',
        controller: EwfProgressBarController,
        controllerAs: 'progressBarCtrl',
        template:'<div class=\"progress progress_width_full\"><div class=progress__loader ng-style=\"{width: progressBarCtrl.progress + \'%\'}\"></div><div class=progress__counter>{{progressBarCtrl.progress}}%</div></div>',
        scope: true
    };
}
